.Layout {
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: stretch;
    justify-content: flex-start;
    position: relative;
}

.Layout--normal {}

.Layout--light {
    background: url("data:image/png;base64,");
}

.Layout--dark {
    background: url("data:image/png;base64,");
}

.Layout__top {
    flex: 0;
    position: relative;
    box-shadow: 0 0 2px #000;
}

.Layout__body {
    flex: 1;
    position: relative;
    overflow: auto;
}

.Layout__bottom {
    flex: 0;
    position: relative;
    box-shadow: 0 0 2px #000;
}