.root {
    position: fixed;
    overflow: hidden;
    user-select: none;
    pointer-events: none;
    background: royalblue;
    z-index: -1;
    left: 0;
    top: 0;
    width: 0;
    height: 0;
}

.watermar {
    font-family: 'Courier New', Courier, monospace;
    font-size: 16px;
    line-height: 24px;
    height: 24px;
    font-weight: bold;
    box-sizing: border-box;
    color: #000;
    background: #000;
    color: #fff;
    position: absolute;
    padding: 0 4px;
    white-space: nowrap;
}

.watermar--bottom {
    left: 0px;
    right: 0px;
    bottom: 0px;
    overflow: hidden;
}

.watermar-top {
    left: 0;
    top: 0;
}