.shade {
    position: absolute;
    background: rgba(0, 0, 0, 0.6);
}

.horizontal {
    position: absolute;
    height: 1px;
    left: 0;
    right: 0;
    background: #33ffd9;
}

.vertical {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 1px;

    background: #33ffd9;
}