.root {
    min-width: 300px;
    padding: 5px;
    box-sizing: border-box;
    position: relative;
}

.section {
    border: 1px dashed #000;
    padding: 5px;
    position: relative;
}

.section+.section {
    margin: 10px 0 0;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.row+.row {
    margin: 10px 0 0;
}